.score_container {
    border: 2px solid var(--header-outline);
    width: 600px;
    max-width: 90%;
    margin: 3em auto 0;
    display: flex;
    justify-content: space-between;
    padding: 1rem 1.5em;
    align-items: center;
    border-radius: 13px;
    -webkit-border-radius: 13px;
    -moz-border-radius: 13px;
    -ms-border-radius: 13px;
    -o-border-radius: 13px;
}
.score_container img {
    width: 90px;
}
.score_container .score {
    background-color: #fff;
    width: 115px;
    height: 90px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
    -webkit-border-radius: 10px;
    -moz-border-radius: 10px;
    -ms-border-radius: 10px;
    -o-border-radius: 10px;
}
.score_container .score span {
    color: var(--score-text);
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 2.5px;
}
.score_container .score h1 {
    color: var(--dark-text);
    font-size: 2.5em;
}
/* start game */
.container {
    height: 60vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
.game {
    background: url("/public/images/bg-pentagon.svg");
    background-repeat: no-repeat;
    background-size: cover;
    width: 280px;
    height: 280px;
    margin: 3em auto 0;
    position: relative;
    z-index: 0;
}
.game div {
    position: absolute;
    padding: 19px;
    background: #fff;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    box-shadow: inset 4px 14px 10px -4px rgb(0 0 0/22%);
    cursor: pointer;
}
.game div,
.result .result_box div {
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.game div {
    width: 90px;
    height: 90px;
}
.result .result_box div {
    width: 150px;
    height: 150px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}
.game div .inner_circle,
.result .result_box div .inner_circle {
    align-items: center;
    background-color: #fff;
    box-shadow: inset 4px 14px 10px -4px rgb(0 0 0/22%);
    cursor: pointer;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    z-index: 5;
}
.game div .inner_circle {
    width: 68px;
    height: 68px;
}
.result .result_box div .inner_circle {
    width: 115px;
    height: 115px;
}

.game a,
.game div,
.result .result_box div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 6;
    align-items: center;
}
.game div img {
    width: 80%;
}

.game .picture-0 {
    top: -28px;
    left: 35%;
}
.game .picture-0,
.result .result_box .picture-0 {
    background: linear-gradient(hsl(39, 89%, 49%), hsl(40, 84%, 53%));
}

.game .picture-1 {
    right: -44px;
    top: 62px;
}
.game .picture-1,
.result .result_box .picture-1 {
    background: linear-gradient(hsl(230, 89%, 62%), hsl(230, 89%, 65%));
}
.game .picture-2 {
    bottom: -27px;
    right: -5px;
}

.game .picture-2,
.result .result_box .picture-2 {
    background: linear-gradient(hsl(349, 71%, 52%), hsl(349, 70%, 56%));
}
.game .picture-3 {
    bottom: -27px;
    left: -7px;
}
.game .picture-3,
.result .result_box .picture-3 {
    background: linear-gradient(hsl(261, 73%, 60%), hsl(261, 72%, 63%));
}
.game .picture-4 {
    left: -40px;
    top: 62px;
}
.game .picture-4,
.result .result_box .picture-4 {
    background: linear-gradient(hsl(189, 59%, 53%), hsl(189, 58%, 57%));
}
.game .picture_container {
    transition: transform 0.25s;
    -webkit-transition: transform 0.25s;
    -moz-transition: transform 0.25s;
    -ms-transition: transform 0.25s;
    -o-transition: transform 0.25s;
}
.game .picture_container:hover {
    transform: translateY(-8px);
    -webkit-transform: translateY(-8px);
    -moz-transform: translateY(-8px);
    -ms-transform: translateY(-8px);
    -o-transform: translateY(-8px);
}

/* result */
.result {
    min-height: 50vh;
    display: flex;
    justify-content: space-evenly;
    margin-top: 3em;
    position: relative;
}
.result .userPick,
.result .pcPick {
    display: flex;
    flex-direction: column;
    text-align: center;
    position: relative;
}
.result > div {
    flex: 1;
}

.result .result_pic {
    top: 30%;
    left: 19%;
}
.result .userPick img,
.result .result_pic img {
    position: relative;
    z-index: 6;
}

.result .result_box div::before {
    width: 140px;
    height: 140px;
}
.result h3 {
    color: #fff;
}
.resultMessage {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.resultMessage h4 {
    font-size: 3em;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 0.4em;
}
.resultMessage a {
    background: #fff;
    color: var(--dark-text);
    border: none;
    width: 190px;
    padding: 12px 24px;
    border-radius: 6px;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 12px;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    -o-border-radius: 6px;
    text-decoration: none;
    text-align: center;
}
/* rules section */
.rules {
    position: fixed;
    bottom: 25px;
    right: 25px;
    border-radius: 7px;
    background: transparent;
    border: 2px solid var(--header-outline);
    color: #fff;
    width: fit-content;
    padding: 9px 31px;
    text-transform: uppercase;
    font-size: 13px;
    cursor: pointer;
}
.popup {
    position: fixed;
    width: 100%;
    background: #00000087;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 100;
    display: none;
}
.rule_popup {
    background: #fff;
    width: 350px;
    max-width: 95%;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 2em;
    z-index: 6;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    border-radius: 7px;
    -webkit-border-radius: 7px;
    -moz-border-radius: 7px;
    -ms-border-radius: 7px;
    -o-border-radius: 7px;
}
.popup.active {
    display: block;
}
.rule_popup .rule_header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1em;
}
.rule_popup .rule_header h2 {
    color: var(--dark-text);
    text-transform: uppercase;
}
.rule_popup .rule_header img {
    width: 16px;
    cursor: pointer;
}
.rule_popup .rule_picture {
    max-width: 100%;
}
@media screen and (max-width: 700px) {
    .result {
        flex-wrap: wrap;
    }
    .result .pcPick,
    .result .userPick {
        flex-basis: 50%;
        height: 36vh;
    }
    .result .result_box div {
        width: 100px;
        height: 100px;
    }
    .result .result_box div .inner_circle {
        width: 80px;
        height: 80px;
    }
    .result .resultMessage {
        flex: 2;
        order: 3;
    }
}
@media screen and (max-width: 400px) {
    .game {
        width: 220px;
        height: 220px;
    }
    .game div {
        width: 80px;
        height: 80px;
    }
    .game div .inner_circle {
        width: 60px;
        height: 60px;
    }

    .game div img {
        width: 70%;
    }
}

.result .result_box .show,
.result .show img {
    width: 0px;
    height: 00px;
}
.pcPick .show .inner_circle {
    background-color:#22212178 !important;
    box-shadow: none !important;
}
.pcPick .show img {
    z-index: -5;
}

